import axios from 'axios'
import { SUPPORTED_NETWORKS_INFO } from '../../../../constants/network'
import React, { useEffect, useState } from 'react'

const useFetchAbi = ({ contractAddress, chainId, abi }) => {
  const [abiState, setAbi] = useState(null)

  useEffect(() => {
    async function fetchAbi() {
      try {
        const chainInfo = SUPPORTED_NETWORKS_INFO[chainId]
        const { data } = await axios.get(
          `${chainInfo.api}/?module=contract&action=getabi&address=${contractAddress}&apikey=${chainInfo.key}`,
        )
        if (data.status === '1') {
          setAbi(data.result)
        } else {
          setAbi(abi)
        }
      } catch (e) {
        console.log('failed to fetch contract abi')
      }
    }
    if (!!contractAddress && !!chainId) {
      fetchAbi()
    }
  }, [contractAddress, chainId])

  return abiState
}

export default useFetchAbi
